$MyLightBlue: #d4dff6;
$MyDarkBlue: #282c34;
$MyBackgroundBlue: #23527c;
$NavBackgroundBlue: #337ab7;

.Navigation {
  display: flex;
  justify-content: center;
  border: 0;
  background: $MyDarkBlue;
  font-family: 'Merienda One', cursive;
  width: 100%;

  a,
  div,
  ul * {
    font-size: 18px;
    background-color: $MyDarkBlue;
    color: $MyLightBlue;
  }
  a:hover {
    background-color: $MyLightBlue;
    color: $MyDarkBlue;
  }
  @media (max-width: 366px) {
    .navbar-brand {
      font-size: 24px;
    }
  }
}

.myNav-container {
  display: grid;
  grid-template-columns: 5fr 3fr;
  grid-auto-columns: 1fr;
  background-color: $MyDarkBlue;
  width: 100%;
  @media (max-width: 1176px) {
    width: 100%;
    padding-top: 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  @media (max-width: 780px) {
    flex-direction: column-reverse;
    justify-items: center;
    margin-top: 0;
  }
  .my-name {
    text-align: left;
    align-self: center;
    font-size: 50px;
    font-family: 'Merienda One', cursive;
    @media (max-width: 700px) {
      span {
        font-size: 40px;
        text-align: center;
        width: 100%;
      }
    }
  }
  .myNav {
    display: flex;
    align-self: flex-end;
    //flex-wrap: wrap;
    border: 25px solid transparent;
    a {
      margin: 10px;
      padding: 10px;
      border: 1px solid $MyLightBlue;
      color: $MyDarkBlue !important;
      background-color: $MyLightBlue;
      min-width: 120px;
      font-size: 18px;
      font-weight: 500;
      &:active {
        color: $MyDarkBlue !important;
        background-color: $MyLightBlue;
      }
      border-radius: 8px;
      -webkit-border-radius: 8px;
      -moz-border-radius: 8px;
      -ms-border-radius: 8px;
      -o-border-radius: 8px;
      @media (max-width: 1280px) {
        font-size: 14px;
        min-width: 96px;
        margin: 5px;
      }
      @media (max-width: 500px) {
        font-size: 12px;
        min-width: 80px;
        margin: 0 2px;
        padding: 5px;
      }
      @media (max-width: 320px) {
        font-size: 14px;
        min-width: 80px;
        margin: 10px 2px 0;
        padding: 5px;
      }
    }
    @media (max-width: 1088px) {
      border: 0px solid;
      align-self: center;
    }
    @media (max-width: 500px) {
      padding: 5px;
    }
  }
}
